.temporary-pass {
    width: 500px;
    margin: 0 auto;
    text-align: center;
    margin-bottom: 100px;
}

.pass__title {
    margin-bottom: 10px;    

    text-align: justify;
    color: #fff;
    font-size: 31px;
    font-weight: 500;
}

.pass__subtitle {
    text-align: justify;
    color: #fff;
    font-weight: 300;
    font-size: 17px;

    margin-bottom: 25px;
}

.select, .input {
    height: 45px;
}

.select, .input, .text-area {
    width: 100%;

    margin: 7px 0;

    box-sizing: border-box;
    border: 1px solid #ddd;
    border-radius: 5px;

    color: #444;
    font-size: 16px;
    padding: 5px 10px;
}

.input {
    box-sizing: border-box;

}

.text-area {
    height: 100px;
    font-size: 14px;
}

.btn-submit {
    height: 45px;

    margin: 10px auto;
    padding: 10px;

    border: none;
    border-radius: 5px;
    background: #0038f0;

    color: #fff;
    font-size: 16px;

    transition-duration: 0.4s;
}

.btn-submit:hover {
    background: none;
    border: 1px solid #fff;
}